@import './normalize.css';
@import './reset.css';

.homeWrapper {
  position: relative;
}

.backgroundImg {
  width: 100vw;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav {
  width: 100%;
  height: 72px;
  position: absolute;
  position: fixed;
  padding: 0 4%;
  display: flex;
  justify-content: space-between;
}
/* 나중에 wrapper 재사용 안하면 PinpointCarousel이랑 합치기 */
.pinpointPinpointCarouselWrapper {
  width: 100vw;
  height: 100vh;
  position: absolute;
  position: fixed;
  flex-direction: column;
}

.pinpointPosition {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-right: 50px;
}

.PinpointCarousel_pinpoint {
  background-color: #ffffff;
  width: 1.25vw;
  height: 1.25vw;
  border-radius: 50%;
  opacity: 0.2;
  margin: 0 0 1.1111111111111112vw;
  cursor: pointer;
}

.pinpointOpacity {
  opacity: 1;
}

.logo {
  width: 94px;
}

.loginBtn {
  background-color: #ffffff;
  color: #121218;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: -0.3px;
  border-radius: 40px;
  height: 45%;
  padding: 5px 18px 6px;
  margin-top: 23px;
}

.contentTitle {
  color: #ffffff;
  margin-bottom: 1.1111111111111112vw;
}

.contentSubTitle {
  color: #ffffff;
  opacity: 0.65;
  margin-bottom: 3.75vw;
}

.button {
  background-color: #f82f62;
  color: #ffffff;
  text-align: center;
  font-size: 1.3888888888888888vw;
  font-weight: 700;
  letter-spacing: -0.0625vw;
  height: 4.166666666666666vw;
  padding: 0 1.5625vw;
  border-radius: 2.083333333333333vw;
}

.contentWrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-row: 2;
  place-self: center;
}

.arrowBtn {
  width: 3.3333333333333335vw;
  height: 3.3333333333333335vw;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  grid-row: 3;
  place-self: center;
  align-self: end;
  margin-bottom: 5.5vw;
  cursor: pointer;
  /* arrow icon */
  background-size: 1.7361111111111112vw 1.7361111111111112vw;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("data:image/svg+xml;base64,");
}

.arrowDir {
  transform: rotate3d(0, 0, 1, -90deg);

}

.lastArrow {
  transform: rotate3d(0, 0, 1, 90deg);
}

.innerContentCenter {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}
